<template>
  <div>
    <NavBar /><br />
    <div class="row">
      <h5 class="text-center font-weight-bold">GESTIÓN CLIENTES</h5>
    </div>
    <br />
    <div class="container-fluid border p-4">
      <div class="col-10 col-m-6 col-lg-8 mx-auto">
        <form @submit.prevent="grabarCliente" class="form-in-line">
          <div class="input-group-text mb-3">
            <span class="input-group-text curstom-span me-2">DNI-NIE</span>
            <input
              type="text"
              class="form-control sm w-25"
              placeholder="DNI-NIE"
              v-model="cliente.dni"
            />
            <span class="input-group-text curstom-span ms-auto me-2"
              >Fecha de Alta</span
            >
            <input
              type="date"
              class="form-control sm w-25"
              placeholder="dd/mm/yyyy"
              v-model="cliente.fechaAlta"
            />
          </div>
          <div class="input-group-text mb-3">
            <span class="input-group-text curstom-span me-2">Apellidos</span>
            <input
              type="text"
              class="form-control sm w-50"
              placeholder="Apellidos"
              v-model="cliente.apellidos"
            />
            <span class="input-group-text curstom-span ms-2 me-2">Nombre</span>
            <input
              type="text"
              class="form-control sm w-50"
              placeholder="Nombre"
              v-model="cliente.nombre"
            />
          </div>
          <div class="input-group-text mb-3">
            <span class="input-group-text curstom-span ms-auto me-2"
              >Direccion</span
            >
            <input
              type="text"
              class="form-control sm"
              placeholder="Direccion"
              v-model="cliente.direccion"
            />
          </div>
          <div class="input-group-text mb-3">
            <span class="input-group-text curstom-span me-2">Provincia</span>
            <select
              id="Provincia"
              class="form-select w-50"
              v-model="cliente.provincia"
            ></select>
            <span class="input-group-text curstom-span ms-3">Municipio</span>
            <select
              id="Municipio"
              class="form-select ms-3 w-50"
              v-model="cliente.municipio"
            ></select>
            <span class="input-group-text curstom-span ms-3">Histórico</span>
            <input
              id="historico"
              type="checkbox"
              class="form-check-input ms-2"
              v-model="isChecked"
            />
          </div>
          <div class="btn-group mb-3 w-50">
            <button
              class="btn btn-primary btn-custom me-2"
              style="border-radius: 20px"
              @click.prevent="grabarCliente"
              type="submit"
            >
              Guardar
            </button>
            <button
              class="btn btn-primary btn-custom me-2"
              style="border-radius: 20px"
              @click.prevent="modificarCliente"
              type="submit"
            >
              Modificar
            </button>
            <button
              class="btn btn-primary btn-custom me-2"
              style="border-radius: 20px"
              @click.prevent="deleteCliente(cliente.dni)"
              type="submit"
            >
              Eliminar
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
  <!-- tabla -->
  <div class="container my-2">
    <div class="table-responsive">
      <table class="table table-striped">
        <thead class="table-info rounded-header">
          <tr>
            <th scope="col" class="w-15 text-center">DNI</th>
            <th scope="col" class="w-25">Apellidos</th>
            <th scope="col" class="w-25">Nombre</th>
            <th scope="col" class="w-20 text-center">Email</th>
            <th scope="col" class="w-10 text-center">Fecha Baja</th>
            <th scope="col" class="pale-yellow">Acciones</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="cliente in filtroClientes" :key="cliente.dni">
            <td class="align-middle">{{ cliente.dni }}</td>
            <td class="align-middle">{{ cliente.apellidos }}</td>
            <td class="align-middle">{{ cliente.nombre }}</td>
            <td class="align-middle">{{ cliente.email }}</td>
            <td class="align-middle">{{ cliente.baja }}</td>
            <td class="text-center align-middle pale-yellow">
              <div>
                <button
                  class="btn btn-warning m-2"
                  @click="seleccionaCliente(cliente)"
                >
                  <i class="fas fa-pencil-alt"></i>
                </button>
                <button
                  class="btn btn-danger m-2"
                  @click="deleteCliente(cliente.dni)"
                >
                  <i class="bi bi-trash"></i>
                </button>
              </div>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
import NavBar from "./NavBar.vue";
import Swal from "sweetalert2";

export default {
  name: "TablaClientes",
  components: {
    NavBar,
  },
  data() {
    return {
      clientes: [
        { dni: "0000000H", apellidos: "Balado Gómez", nombre: "Juan", email: "asdf@gasdg.es", baja: "" },
        { dni: "0000000H", apellidos: "Gil Gómez", nombre: "Alvaro", email: "asdfasff@fassg.es", baja: "" },
        { dni: "0000000H", apellidos: "Balado Gómez", nombre: "Maria", email: "asdf@gasdg.es", baja: "04/11/2024" },
      ],
      cliente: {
        dni: "", alta: "", apellidos: "", nombre: "", direccion: "", email: "", provincia: "", municipio: "", baja: "",
      },
      errores: [],
      isChecked: false,
      clienteSeleccionado: null,
    };
  },
  methods: {
    grabarCliente() {
      if (this.validarDNI(this.cliente.dni)) {
        this.cliente.fechaAlta = new Date().toLocaleDateString();
        this.clientes.push({ ...this.cliente });
        this.cliente = {};
      }
    },

    seleccionaCliente(cliente) {
      this.cliente = { ...cliente };
      this.clienteSeleccionado = cliente.dni;
    },

    modificarCliente() {
      if (!this.clienteSeleccionado) {
        this.mostrarAlerta("Error", "Seleccione un cliente para modificar.", "error");
        return;
      }

      const index = this.clientes.findIndex((c) => c.dni === this.clienteSeleccionado);
      if (index !== -1) {
        this.clientes.splice(index, 1, { ...this.cliente });
        this.resetForm();
        this.mostrarAlerta("Éxito", "Cliente modificado con éxito.", "success");
        this.clienteSeleccionado = null;
      }
    },

    deleteCliente(dni) {
      this.clientes = this.clientes.filter((cliente) => cliente.dni !== dni);
      this.mostrarAlerta("Éxito", "Cliente eliminado con éxito.", "success");
    },

    resetForm() {
      this.cliente = { dni: "", alta: "", apellidos: "", nombre: "", direccion: "", email: "", provincia: "", municipio: "", baja: "" };
    },

      validarDNI(dni) {
      if (dni === "") {
        return true;
      }
      dni = dni.toUpperCase();
      this.cliente.dni = dni;
      const dniRegex = /^[XYZ0-9][0-9]{7}[A-Z]$/;

      if (!dniRegex.test(dni)) {
        this.mostrarAlerta(
          "Error",
          "El NIE/DNI con formato no válido.",
          "error"
        );
        return false;
      }

      let dniNum = dni.substring(0, 8);
      const letra = dni.charAt(8);
      if (dniNum.charAt(0) === "X") {
        dniNum = dniNum.substring(1, 8);
        dniNum = "0" + dniNum;
      } else if (dniNum.charAt(0) === "Y") {
        dniNum = dniNum.substring(1, 8);
        dniNum = "1" + dniNum;
      } else if (dni.charAt(0) === "Z") {
        dniNum = dniNum.substring(1, 8);
        dniNum = "2" + dniNum;
      }

      const letras = "TRWAGMYFPDXBNJZSQVHLCKE";
      const letraCalculada = letras[dniNum % 23];
      if (letra !== letraCalculada) {
        this.mostrarAlerta("Error", "DNI/NIE Incorrecto.", "error");
        return false;
      }

      return true;
    },
    mostrarAlerta(titulo, mensaje, icono) {
      Swal.fire({
        title: titulo,
        text: mensaje,
        icon: icono,
        customClass: {
          container: "custom-alert-container",
          popup: "custom-alert-popup",
          modal: "custom-alert-modal",
        },
      });
    },
  },
  computed: {
    filtroClientes() {
      return this.isChecked ? this.clientes : this.clientes.filter((cliente) => !cliente.baja);
    },
  },

};
</script>
